import React from 'react'

function ReuasableSubHeader(props) {
    const data = props.Sub_data;
    console.log(data);
    return (
        <div className="ritekhela-subheader">
        <div className="container">
            <div className="row">
                <div className="col-md-12">
                   {data.map(data =><h1>{data.header}</h1>)}
                    <ul className="ritekhela-breadcrumb">
                        <li>{data.map(data =><a href="#">{data.title}</a>)}</li>
                        <li>{data.map(data =><a href="#">{data.path}</a>)}</li>
                        {data.map(data => <li>{data.path_two}</li>)}
                    </ul>
                </div>
            </div>
        </div>
    </div>
    )
}

export default ReuasableSubHeader
