import React ,{useState,useEffect} from 'react'
import {Link} from "react-router-dom"
import Footer from '../components/Footer'
import Header from '../components/Header'
import LoginModalBox from '../components/LoginModalBox'
import PlayersContent from '../components/PlayersContent'
import ReuasableHeader from '../components/ReuasableHeader'
import ReuasableSubHeader from '../components/ReuasableSubHeader'
import SearchBox from '../components/SearchBox'
import SignUpModaBox from '../components/SignUpModaBox'
import axios from 'axios'
function Players() {
    let HeaderData  ={
        login :'Login',
        signup :'Signup',
         home:'Home', 
        features:'Teams',
        players:'Players',
        ournews:'Matche ',
        ourshop:'Our Shop' ,
        contactus:'Contact us',
    };
        
      

      let SubHeaderData  =[{
        header:'PLAYERS',
        title :'HOME',
        path :'PLAYER',
         path_two:'PLAYER GRID',}
        
      ];
      //const toke = localStorage.getItem('user-info');
      //const user_token = JSON.parse(toke);
      const [players ,setPlayers] = useState()

      useEffect(() => {
        get_all_players();
        
      }, [])
      async function get_all_players()
      {
          try
          {
              let result = await axios.get("http://127.0.0.1:8000/api/show_players",{
              method:'GET',
              headers:
              {
                  "Content-Type": "application/json",
                  "Accept": "application/json",
                    
              }
              })
              let players_list = await result.data
             // console.log(players_list)
            const  data = result;//.school
            setPlayers(players_list);
            console.log("pending_teams :" ,data)  
          }catch(err)
          {
              console.error(err);
          }
      } 
      console.log("players",players); 
    return (
        <>
        <div  className="ritekhela-wrapper">
            {/* <Header/> */}
            <ReuasableHeader headers ={HeaderData}/>
            {/* <SubHeader/> */}
            <ReuasableSubHeader Sub_data = {SubHeaderData}/>
            {/* {players.map(player => <h1>{player.first_name}</h1>)} */}
            {players && <PlayersContent players_data = {players}/>}
            <Footer/>
        </div>
        <LoginModalBox/>
        <SignUpModaBox/>
        <SearchBox/>
     
    
</>
        

    )
}

export default Players
