import React,{useState} from 'react'
import {useHistory} from "react-router-dom"
function SignUpModaBox() {
    const [User_name, setUser_name] = useState();
    const [email, setEmail] = useState();
    const [password, setPassword] = useState();
    const [confirm_password,setConfirm_password]=useState();
    const history = useHistory()
   async function Signup()
   {
       let credantials = {User_name,email,password,confirm_password};
       console.log(credantials);
           try{
           let result = await fetch("http://127.0.0.1:8000/api/auth/register",{
           method:'POST',
           body:JSON.stringify({
               name: credantials.User_name,
               email: credantials.email,
               password: credantials.password,
               password_confirmation:credantials.confirm_password,
           }),
           headers:{
               "Content-Type": "application/json",
               "Accept": "application/json"
                    }
           })
           result = await result.json()
           console.log("result",result);
           localStorage.setItem("user-info",JSON.stringify(result))
           //history.push("/admin")
        }catch(err){
           console.error(err);
        }
       
     
   } 
    return (
        <div className="loginmodalbox modal fade" id="ritekhelamodalrg" tabIndex="-1">
       <div className="modal-dialog modal-dialog-centered" role="document">
          <div className="modal-content">
             <div className="modal-body ritekhela-bgcolor-two">
                <h5 className="modal-title">Sign Up Now</h5>
                <a href="#" className="close ritekhela-bgcolor-two" data-dismiss="modal" aria-label="Close">
                    <i className="fa fa-times"></i>
                </a>
                <div className="loginmodalbox-search">
                <input
                type="text"
                value={User_name} 
                placeholder="You Full Name"
                onChange={(e) => setUser_name(e.target.value)}
                />
                <input
                type="text"
                value={email} 
                placeholder="Email"
                onChange={(e) => setEmail(e.target.value)}
                />
                <input
                type="password"
                value={password} 
                placeholder="Password"
                onChange={(e) => setPassword(e.target.value)}
                />
                <input
                type="password"
                value={confirm_password} 
                placeholder="confirm your password"
                onChange={(e) => setConfirm_password(e.target.value)}
                />

                   
                <input type="submit" onClick={Signup} value="Sign Up" className="ritekhela-bgcolor"/>
                <a href="#" className="loginmodalbox-forget">Forget Password?</a>
                </div>
             </div>
          </div>
       </div>
    </div>
    )
}

export default SignUpModaBox
