import React,{useState} from 'react'
import ReuasbleTitle from './ReuasbleTitle';
function AddTeams() {
    const [titles , setTitle] = useState([
        {title : 'COACH Add Teams'}]);
        const [teamName, setTeamName] = useState();
        const [teamDescription, setTeamDescription] = useState();
        const toke = localStorage.getItem('user-info');
        const user_token = JSON.parse(toke);
        //console.log(user_token.access_token);
        console.log('token is : ',toke); 

        async function AddTeam(e)
        {
            e.preventDefault();
            let credantials = {teamName,teamDescription}
            console.log(credantials)
            try
            {
                let result = await fetch("http://127.0.0.1:8000/api/auth/add_teams",{
                    method: 'POST',
                    headers:
                    {
                        "Content-Type": "application/json",
                        "Accept": "application/json",
                        "Authorization" : "bearer"+ user_token.access_token, 
                    },
                    body:JSON.stringify({
                        team_name : credantials.teamName,
                        description: credantials.teamDescription,
                    })
                })
                result = await result.json()
                console.log(result)
            }
            catch(err)
            {
                console.error(err)
            }

        }
    return (
        <div className="ritekhela-main-content">

        
        <div className="ritekhela-main-section ritekhela-fixture-slider-full">
            <div className="container">
        <div className="widget widget_newsletter">
      
        <ReuasbleTitle titles={titles}/>
     
        <form>
            <label>Add Team</label>
            <p>Add Your Teams Here</p>
            <input
             type="text"
             value={teamName} 
             placeholder="Team Name"
             onblur="if(this.value == '') { this.value ='Your Name'; }"
             onfocus="if(this.value =='Your Name') { this.value = ''; }"
             onChange={(e) => setTeamName(e.target.value)}
             />
            <input
            type="text"
            value={teamDescription}
            placeholder="Description About Team "
            onblur="if(this.value == '') { this.value ='Enter Your email'; }" 
            onfocus="if(this.value =='Enter Your email') { this.value = ''; }"
            onChange ={(e) => setTeamDescription(e.target.value)}
            />
            <input type="submit" onClick={AddTeam} value="Add Team"/>
        </form>
    </div>
    </div>
    </div>
    </div>
    )
}

export default AddTeams
