import React from 'react'

function SearchBox() {
    return (
        <div className="loginmodalbox modal fade" id="ritekhelamodalsearch" tabIndex="-1">
        <div className="modal-dialog modal-dialog-centered" role="document">
           <div className="modal-content">
              <div className="modal-body ritekhela-bgcolor-two">
                 <h5 className="modal-title">Search Here</h5>
                 <a href="#" className="close ritekhela-bgcolor-two" data-dismiss="modal" aria-label="Close">
                     <i className="fa fa-times"></i>
                 </a>
                 <form>
                     <input type="text"
                       placeholder="Search here"
                       />
                     <input type="submit" value="Search Now" className="ritekhela-bgcolor"/>
                 </form>
              </div>
           </div>
        </div>
     </div>
    )
}

export default SearchBox
