import React , {useState,useEffect}from 'react'
import { Link } from 'react-router-dom'
import Footer from '../components/Footer'
import Header from '../components/Header'
import LoginModalBox from '../components/LoginModalBox'
import ReuasableHeader from '../components/ReuasableHeader'
import ReuasableSubHeader from '../components/ReuasableSubHeader'
import SearchBox from '../components/SearchBox'
import SignUpModaBox from '../components/SignUpModaBox'
import TeamsContent from '../components/TeamsContent'
import axios from 'axios'
function Teams() {
    let HeaderData  ={
        login :'Login',
        signup :'Signup',
         home:'Home', 
        features:'Teams',
        players:'Players',
        ournews:'Matche ',
        ourshop:'Our Shop' ,
        contactus:'Contact us'
    };
      
      let SubHeaderData  =[{
        header:'TEAMS',
        title :'HOME',
        path :'PLAYER',
         path_two:'TEAM',}
        
      ];

      const [teams ,setTeams] = useState()

      useEffect(() => {
        get_all_Teams();
        
      }, [])
      async function get_all_Teams()
      {
          try
          {
              let result = await axios.get("http://127.0.0.1:8000/api/show_teams",{
              method:'GET',
              headers:
              {
                  "Content-Type": "application/json",
                  "Accept": "application/json",
                    
              }
              })
              let teams_list = await result.data
             // console.log(players_list)
            const  data = result;//.school
            setTeams(teams_list);
            console.log("pending_teams :" ,data)  
          }catch(err)
          {
              console.error(err);
          }
      } 
      console.log("teams",teams); 
    return (
        <>
        <div className="ritekhela-wrapper">
            {/* <Header/> */}
            <ReuasableHeader headers={HeaderData}/>
            {/* <SubHeaderTeams/> */}
            <ReuasableSubHeader Sub_data = {SubHeaderData} />
            {teams && <TeamsContent teams_data ={teams}/>}
            <Footer/>

            
        </div>
        <LoginModalBox/>
        <SignUpModaBox/>
        <SearchBox/>
      
        </>
    )
}

export default Teams
