import React ,{useState} from 'react'
import ReuasbleTitle from './ReuasbleTitle';

function Competitions(props) {
    const competition = props.data
    const join = (e)=> props.enroll(e.target.id)
    const [titles , setTitle] = useState([
        {title : 'Coordinator Page  Add Coach'}]);
    return (
        <> 
            <div className="ritekhela-main-content">
            <div className="ritekhela-main-section ritekhela-fixture-list-full">
            <div className="container">
            <ReuasbleTitle   titles={titles} />
            <div>
        <table >
        <thead>
          <tr>
            <th scope="col">ID</th>
            <th scope="col">Competition Name</th>
            <th scope="col">Competition Description</th>
            <th scope="col">Enrol</th>
            {/* <th scope="col">Decline</th> */}
          </tr>
        </thead>
        {competition.map(comp=>
        <tbody>
          <tr>
            <th scope="row" >{comp.id}</th>
            <td>{comp.name}</td>
            <td>{comp.description}</td>
            <td>
            <p>
            <a href ="#"><i className="fas fa-check" style={{color: "green"}} id = {comp.id} onClick={join} ></i></a>
            </p>
            </td> 
          </tr>
        </tbody>
        )}
      </table>
      </div>                
        </div>
        </div>
        </div>
        </>
    )
}

export default Competitions
