import  React,{useState} from 'react';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import ReuasbleTitle from './ReuasbleTitle';
function Inputs() {
  const [titles , setTitle] = useState([
    {title : 'Coordinator Page'}]);

    const [schoolName, setSchoolName] = useState();
    const [schoolAddress, setSchoolAddress] = useState();
    const [schoolPhone, setSchoolPhone] = useState();
    const toke = localStorage.getItem('user-info');
    const user_token = JSON.parse(toke);
    //console.log(user_token.access_token);
    console.log('token is : ',toke);
    async function Add_school(e)
    { 
      e.preventDefault();
      let credantials = {schoolName,schoolPhone,schoolAddress};
       console.log(credantials);
           try{
           let result = await fetch("http://127.0.0.1:8000/api/auth/add_schools",{
           method:'POST',
           body:JSON.stringify({
               school_name: credantials.schoolName,
               school_address: credantials.schoolAddress,
               school_phone: credantials.schoolPhone,
           }),
           headers:{
               "Content-Type": "application/json",
               "Accept": "application/json",
               "Authorization" : "bearer"+ user_token.access_token,
                    }
           })
           result = await result.json()
           console.log("result",result);
           //localStorage.setItem("user-info",JSON.stringify(result))
           //history.push("/admin")
        }catch(err){
           console.error(err);
        }

    }
    return (
      <div className="ritekhela-main-content">

        
      <div className="ritekhela-main-section ritekhela-fixture-slider-full">
          <div className="container">
      <div className="widget widget_newsletter">
      <ReuasbleTitle titles={titles}/>
      <form>
          <label>School Information</label>
          <p>Fill The Following Fields Please</p>
          <input
           type="text"
           value={schoolName} 
           placeholder="School Name"
           onblur="if(this.value == '') { this.value ='Your Name'; }"
           onfocus="if(this.value =='Your Name') { this.value = ''; }"
           onChange={(e) => setSchoolName(e.target.value)}
           />
          <input
          type="text"
          value={schoolAddress}
          placeholder="School Address"
          onblur="if(this.value == '') { this.value ='Enter Your email'; }" 
          onfocus="if(this.value =='Enter Your email') { this.value = ''; }"
          onChange ={(e) => setSchoolAddress(e.target.value)}
          />
                <input
           type="text"
           value={schoolPhone} 
           placeholder="phone number"
           onblur="if(this.value == '') { this.value ='Your Name'; }"
           onfocus="if(this.value =='Your Name') { this.value = ''; }"
           onChange={(e) =>setSchoolPhone(e.target.value)}
           /> 
          <input type="submit" onClick={Add_school} value="Add School"/>
      </form>
  </div>
  </div>
  </div>
  </div>
    )
}

export default Inputs
