import React ,{useState} from 'react'
import AddTeams from '../components/AddTeams'
import CoachInputs from '../components/CoachInputs'
import CoordinatorHeader from '../components/CoordinatorHeader'
import Footer from '../components/Footer'

function Coach() {

    let HeaderData  ={
        addSchool : 'Add Player',
        addCoach :'Add Team',
        newcomp : 'Results',
        yourTeams :'Your Teams',
        yourPlayers :'Your Players',
        profile : 'Profile'
     };

    const [showAddcoach , setShowAddCoach] = useState(false);
    const [showAddSchool, setShowAddSchool] = useState(true);
    function handleChangeStatus(){
        setShowAddCoach(!showAddcoach)
        setShowAddSchool(false)
    }
    function handleChangeStatusTable(){
        setShowAddSchool(!showAddSchool)
        setShowAddCoach(false)
    }
    return (
        <div>
            <CoordinatorHeader data = {HeaderData}  onAdd ={handleChangeStatus} 
            onSchool ={handleChangeStatusTable}/>
            {showAddSchool && <CoachInputs/>}
            {showAddcoach && <AddTeams/>}
            <Footer/>
            
        </div>
    )
}

export default Coach
