import React, {useState }from 'react'
import Radio from '@material-ui/core/Radio'
import DateFnsUtils from '@date-io/date-fns'
import { alpha } from '@material-ui/core/styles'
import {
    DatePicker,
    TimePicker,
    DateTimePicker,
    MuiPickersUtilsProvider,
    KeyboardDatePicker,
  } from '@material-ui/pickers';
import DateMomentUtils from '@date-io/moment';
import ReuasbleTitle from './ReuasbleTitle';

function CoachInputs() {

    const [firstname, setFirstname] = useState();
    const [lastname, setLastname] = useState();
    const [playerClass, setPlayerClass] = useState();
    const [gender, setGender] = useState();
    const [currentDate, setCurrentDate] = useState(new Date());
    const [titles , setTitle] = useState([
        {title : 'coach'}
    ]);
    const [month, day, year] = [currentDate.getMonth(), currentDate.getDate(), currentDate.getFullYear()];
    const date_of_birth = year+"-"+month+"-"+day; 
    console.log(month);
    console.log(day);
    console.log(year);
    console.log(date_of_birth);
    //console.log(currentDate)
    const toke = localStorage.getItem('user-info');
    const user_token = JSON.parse(toke);
    //console.log(user_token.access_token);
    console.log('token is : ',toke);
    const handelGender = (e)=>{
        console.log(e.target.value)
        setGender(e.target.value)
       
    }
    console.log(currentDate)
    const [selectedFile, setSelectedFile] = useState(null);//useState(null)
    //console.log(selectedFile.name)
    
    
    async function Add_player(e)
    {
    //     const ImageInfo ={
    //     ImageName : selectedFile.name,
    //     ImageType : selectedFile.type,
    // }
    
        e.preventDefault();
        let credantials = {firstname,lastname,gender,date_of_birth,playerClass,selectedFile};//selectedFile instead of ImageInfo
       console.log(credantials);
           try{
           let result = await fetch("http://127.0.0.1:8000/api/auth/add_players",{
           method:'POST',
           body:JSON.stringify({
               first_name: credantials.firstname,
               last_name: credantials.lastname,
               gender: credantials.gender,
               class:credantials.playerClass,
               date_of_birth:credantials.date_of_birth,
               file: credantials.selectedFile,
           }),
           headers:{
               "Content-Type": "application/json",
               "Accept": "application/json",
               "Authorization" : "bearer"+ user_token.access_token,
                    }
           })
           result = await result.json()
           console.log("result",result);
           //localStorage.setItem("user-info",JSON.stringify(result))
           //history.push("/admin")
        }catch(err){
           console.error(err);
        }
       

    }
    return (
        <div className="ritekhela-main-content">

        
        <div className="ritekhela-main-section ritekhela-fixture-slider-full">
            <div className="container">
        <div className="widget widget_newsletter">
      
        <ReuasbleTitle titles={titles}/>
     
        <form enctype="multipart/form-data">
            <label>Add Player</label>
            <p>Fill The Fields With Player Information Please</p>
            <input
             type="text"
             value={firstname} 
             placeholder="Player First Name"
             onblur="if(this.value == '') { this.value ='Your Name'; }"
             onfocus="if(this.value =='Your Name') { this.value = ''; }"
             onChange={(e) => setFirstname(e.target.value)}
             />
             <input
             type="text"
             value={lastname} 
             placeholder="Player Last Name"
             onblur="if(this.value == '') { this.value ='Your Name'; }"
             onfocus="if(this.value =='Your Name') { this.value = ''; }"
             onChange={(e) => setLastname(e.target.value)}
             />
            <div>
            <p>
            <span>Male</span>
            <Radio
            value ="male"
            checked ={gender === 'male'}
            color ="#999"
            onChange ={handelGender}
            />
            <span>Female</span>
            <Radio
            value ="female"
            checked ={gender === 'female'}
            color ="#999"
            onChange ={handelGender}
            />
            </p>
            </div>
            {/* // utils={DateMomentUtils */}
            <MuiPickersUtilsProvider  utils={DateFnsUtils}>
            <DatePicker  
            value={currentDate} 
            onChange={setCurrentDate}
            
            
             />
            </MuiPickersUtilsProvider>
            <input
            type="text"
            value={playerClass}
            placeholder="player class ex: grade 10"
            onblur="if(this.value == '') { this.value ='Enter Your email'; }" 
            onfocus="if(this.value =='Enter Your email') { this.value = ''; }"
            onChange ={(e) => setPlayerClass(e.target.value)}
            /> 
            <br/>
            <input
            type="file"
            onChange ={(e) =>setSelectedFile(e.target.files[0]['name']) }
            name ="image"
            /> 
            <br/>
            <input type="submit" onClick={Add_player}value="Add Player"/>
        </form>
    </div>
    </div>
    </div>
    </div>
    )
}

export default CoachInputs
