import React ,{useState, useEffect}from 'react'
import AdminHeader from '../components/AdminHeader'
import Footer from '../components/Footer'
import Table from '../components/Table'
import SearchBox from '../components/SearchBox'
import CreateCompetition from '../components/CreateCompetition'
import axios from 'axios'
import SetGames from '../components/SetGames'
import SetResults from '../components/SetResults'
function AdminPage() {
    
    // let HeaderData  =[{
    //     login :'Login',
    //     signup :'Signup',
    //      home:'Homeeeeeeeeeeeeeeeee', 
    //     features:'Features1',
    //     players:'Players1',
    //     ournews:'Owr News1 ',
    //     ourshop:'Our Shop 1' ,
    //     contactus:'Contact us1'}
        
    //   ];
const toke = localStorage.getItem('user-info');
const user_token = JSON.parse(toke);
const [schools,setSchools] = useState(null)
const [pending_schools,setPendingSchool] = useState(null)
useEffect(() => {
  fetchSchools();
  fetch_pending_teams();
  fetch_games();
  
}, [])

/////////////////////////////
async function Approve(school_id)
{

  const id = school_id
  //console.log(e.target.id)
  let credantials = {id};
  console.log(credantials);
      try{
      let result = await fetch("http://127.0.0.1:8000/api/auth/approve_account",{
      method:'POST',
      body:JSON.stringify({
          id: credantials.id,
      }),
      headers:{
          "Content-Type": "application/json",
          "Accept": "application/json",
          "Authorization" : "bearer"+ user_token.access_token, 
               }
      })
      result = await result.json()
      console.log("result",result);
      fetchSchools();
     
   }catch(err){
      console.error(err);
   }
}
//////////////////////////////

/////////////////////////////
async function Decline(school_id)
{

  const id = school_id
  //console.log(e.target.id)
  let credantials = {id};
  console.log(credantials);
      try{
      let result = await fetch("http://127.0.0.1:8000/api/auth/decline_account",{
      method:'POST',
      body:JSON.stringify({
          id: credantials.id,
      }),
      headers:{
          "Content-Type": "application/json",
          "Accept": "application/json",
          "Authorization" : "bearer"+ user_token.access_token, 
               }
      })
      result = await result.json()
      console.log("result",result);
      fetchSchools();
     
   }catch(err){
      console.error(err);
   }
}
//////////////////////////////

async function fetchSchools()
{
    try
    {
        let result = await axios.get("http://127.0.0.1:8000/api/auth/show_school",{
        method:'GET',
        headers:
        {
            "Content-Type": "application/json",
            "Accept": "application/json",
            "Authorization" : "bearer"+ user_token.access_token,  
        }
        })
        let skl = await result.data
        console.log(skl)
      const  data = result;//.school
      setSchools(skl);
      console.log("result :" ,data)  
    }catch(err)
    {
        console.error(err);
    }
}
//fetchSchools().then(schoolssssssss => {console.log("schooooooooools",schools)});
/////////////////////////////////////////
async function fetch_pending_teams()
{
    try
    {
        let result = await axios.get("http://127.0.0.1:8000/api/auth/get_enroll_comp",{
        method:'GET',
        headers:
        {
            "Content-Type": "application/json",
            "Accept": "application/json",
            "Authorization" : "bearer"+ user_token.access_token,  
        }
        })
        let pending_team = await result.data
        console.log(pending_team)
      const  data = result;//.school
      setPendingSchool(pending_team);
      console.log("pending_teams :" ,data)  
    }catch(err)
    {
        console.error(err);
    }
}
////////////////////////////////////////
/////////////////////////////////////////
const [games,setGames] = useState();
async function fetch_games()
{
    try
    {
        let result = await axios.get("http://127.0.0.1:8000/api/auth/show_games",{
        method:'GET',
        headers:
        {
            "Content-Type": "application/json",
            "Accept": "application/json",
            "Authorization" : "bearer"+ user_token.access_token,  
        }
        })
        let games_data = await result.data
        console.log(games_data)
      const  data = result;//.school
      setGames(games_data);
      console.log("games :" ,data)  
    }catch(err)
    {
        console.error(err);
    }
}
console.log("gamessssssssssssssssss : ",games);
//games.team_two[0][0]['team_name']
////////////////////////////////////////
console.log("schools :" ,schools)
console.log("pendingteams :",pending_schools)
const [showComp, setshowComp] = useState(true);
const [showTable , setshowTable] = useState(false);
const [showPendingsSkl , setshowPendingsSkl] = useState(false);

    function handleChangeStatus(){
        setshowTable(!showTable)
        setshowComp(false)
        setshowPendingsSkl(false)
    }
    function handleChangeStatusTable(){
        setshowComp(!showComp)
        setshowTable(false)
        setshowPendingsSkl(false)
    }
    function handleChangeStatusPending(){
        setshowPendingsSkl(!showPendingsSkl)
        setshowComp(false)
        setshowTable(false)
        
    }
    return (
        <>
        <div className="ritekhela-wrapper">
        <AdminHeader  onAdd ={handleChangeStatus} 
                onSchool ={handleChangeStatusTable}
                onSetgame = {handleChangeStatusPending}/>
           {/* {games.game_id.map(game=> <h1>{game}</h1>)} */}
        {showTable &&<Table data ={schools} onDelete ={Decline} onApprove ={Approve}/>}
        {showComp && <CreateCompetition/>}
        {showPendingsSkl && pending_schools && <SetGames comp ={pending_schools} />}
        {/* {schools && <div>{schools.map(user=><h1>{user.school_name}</h1>)}</div> } */}
        {/* {pending_schools.map(pen_skl =><h1>{pen_skl}</h1>)} */}
        { games && <SetResults score = {games}/>}
        <Footer/>
        </div>
        <SearchBox/>
        </>
    )
}

export default AdminPage
