import React , {useState , useEffect} from 'react'
import AddCoach from '../components/AddCoach'
import Competitions from '../components/Competitions'
import CoordinatorHeader from '../components/CoordinatorHeader'
import Footer from '../components/Footer'
import Inputs from '../components/Inputs'
import axios from 'axios'
function Coordinator() {
    let HeaderData  ={
         addSchool : 'Add School',
         addCoach :'Add Coach',
         newcomp : 'New Competitions',
         yourTeams :'Your Teams',
         yourPlayers :'Your Players',
         profile : 'Profile'
      };
  
    const [competitions,setCompetitions] = useState(null);
    const toke = localStorage.getItem('user-info');
    const user_token = JSON.parse(toke);
    useEffect(() => {
        fetchCompetitions();
  
    }, [])
    console.log("usersssssssssssssss",user_token.user.school_id)

    async function fetchCompetitions()
    {
    try
    {
        let result = await axios.get("http://127.0.0.1:8000/api/auth/show_competitions",{
        method:'GET',
        headers:
        {
            "Content-Type": "application/json",
            "Accept": "application/json",
            "Authorization" : "bearer"+ user_token.access_token,  
        }
        })
        let comp = await result.data
        console.log(comp)
      const  data = result;//.school
      setCompetitions(comp);
      console.log("result :" ,data)  
    }catch(err)
    {
        console.error(err);
    }
}
console.log("competitions :" ,competitions)
/////////////////////////
async function Enroll(comp_id)
{
  const id = comp_id
  const school = user_token.user.school_id
  console.log(id)

  let credantials = {id,school};
       console.log(credantials);
           try{
           let result = await fetch("http://127.0.0.1:8000/api/auth/enroll_comp",{
           method:'POST',
           body:JSON.stringify({
               school_id: credantials.school,
               competition_id: credantials.id,
             
           }),
           headers:{
               "Content-Type": "application/json",
               "Accept": "application/json",
               "Authorization" : "bearer"+ user_token.access_token,  
                    }
           })
           result = await result.json()
           console.log("result",result);
         
        }catch(err){
           console.error(err);
        }
 
}
//////////////////////////////
const [showAddcoach , setShowAddCoach] = useState(false);
const [showCompetitions , setshowCompetitions] = useState(false);
const [showAddSchool, setShowAddSchool] = useState(true);
function handleChangeStatusAddSkl(){
    setShowAddCoach(!showAddcoach)
    setshowCompetitions(false)
    setShowAddSchool(false)
}
function handleChangeStatusAddCoach(){
    setShowAddSchool(!showAddSchool)
    setShowAddCoach(false)
    setshowCompetitions(false)
}
function handleChangeStatusComp(){
    setshowCompetitions(!showCompetitions)
    setShowAddCoach(false)
    setShowAddSchool(false)
    
}
    return (
        <>
        <div>
            <CoordinatorHeader 
            data = {HeaderData} 
            onAdd ={handleChangeStatusAddSkl} 
            onSchool ={handleChangeStatusAddCoach}
            onSetgame = {handleChangeStatusComp}/>
            { showAddSchool && <Inputs/>}
           { showAddcoach && <AddCoach />}
           { showCompetitions && competitions && <Competitions data ={competitions} enroll = {Enroll}/>}
            <Footer/>
        </div>
       
        </>
    )
}

export default Coordinator
