import React from 'react'
import {Link} from 'react-router-dom'
function AdminHeader(props) {
    const add = props.onAdd
    const adds = props.onSchool
    const setgames = props.onSetgame
    return (
        <header id="ritekhela-header" className="ritekhela-header-one">
                   <div className="ritekhela-topstrip">
        <div className="container">
            <div className="row">
                
                <aside className="col-md-6">
                    <strong>YOUTH COMPETITIONS</strong>
                    <div className="ritekhela-latest-news-slider">
                        <div className="ritekhela-latest-news-slider-layer">your welcome lets meet !! </div>
                        <div className="ritekhela-latest-news-slider-layer">keep in touch with the latest competitions  </div>
                    </div>
                </aside>
                <aside className="col-md-6">
                    <ul className="ritekhela-user-strip">
                       
                        <li><a href="#" data-toggle="modal" data-target="#ritekhelamodalrg"><i className="fa fa-sign-in-alt"></i> Logout</a></li>
                    </ul>
                </aside>

            </div>
        </div>
    </div>
            <div className="ritekhela-main-header">
        <div className="container">
            <div className="row">
                <div className="col-md-12">
                    <a href="index-2.html" className="ritekhela-logo"><img src="images/logo.png" alt=""/></a>
                    <div className="ritekhela-right-section">
                        <div className="ritekhela-navigation">
                            <span className="ritekhela-menu-link">
                                <span className="menu-bar"></span>
                                <span className="menu-bar"></span>
                                <span className="menu-bar"></span>
                            </span>
                            <nav id="main-nav">
                                <ul id="main-menu" className="sm sm-blue">
                                    <li className="active"><a href="#" onClick={adds}>New Competition</a></li>
                                    <li className="megamenu-wrap"><a href="#"onClick={add} >New Schools</a>  
                                    </li>
                                    <li> 
                                        <a href="#" onClick={setgames}>Matches</a>
                                     
                                    </li>
                                    <li><a href="#">View Teams</a>
                                       
                                    </li>
                                    <li><a href="#">View Results</a>
                                    
                                    </li>
                                    <li><a href="contact-us.html">Contact Us</a></li>
                                </ul>
                            </nav>
                        </div>
                        <ul className="ritekhela-navsearch">
                            <li><a href="#" className="ritekhela-open-cart"><i className="fa fa-bell"></i></a></li>
                            <li><a href="#" data-toggle="modal" data-target="#ritekhelamodalsearch"><i className="fa fa-search"></i></a></li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    </div>

        </header>
    )
}

export default AdminHeader
