import React, {useState }from 'react'
import DateFnsUtils from '@date-io/date-fns'
import { alpha } from '@material-ui/core/styles'
import {
    DatePicker,
    TimePicker,
    DateTimePicker,
    MuiPickersUtilsProvider,
    KeyboardDatePicker,
  } from '@material-ui/pickers';
import DateMomentUtils from '@date-io/moment';
import ReuasbleTitle from './ReuasbleTitle';

function CreateCompetition() {
    
    const [CompetitionName, setCompetitionName] = useState();
    const [comp_description, setComp_description] = useState();
    const [start_date, setStart_date] = useState(new Date());
    const [end_date, setEnd_date] = useState(new Date());
    const [titles , setTitle] = useState([
        {title : 'Admin'}
    ]);
    const [S_month, S_day, S_year] = [start_date.getMonth(), start_date.getDate(), start_date.getFullYear()];
    const Start = S_year+"-"+S_month+"-"+S_day; 
    const [E_month, E_day, E_year] = [end_date.getMonth(), end_date.getDate(), end_date.getFullYear()];
    const End = E_year+"-"+E_month+"-"+E_day; 
    // console.log(month);
    // console.log(day);
    // console.log(year);
    const toke = localStorage.getItem('user-info');
    const user_token = JSON.parse(toke);
    async function AddCompetition(e){
         e.preventDefault();
         let credantials = {CompetitionName,comp_description,Start,End};
         console.log(credantials);
           try{
           let result = await fetch("http://127.0.0.1:8000/api/auth/create_competitions",{
           method:'POST',
           body:JSON.stringify({
               name: credantials.CompetitionName,
               description: credantials.comp_description,
               start_date: credantials.Start,
               end_date:credantials.End,
              
           }),
           headers:{
               "Content-Type": "application/json",
               "Accept": "application/json",
               "Authorization" : "bearer"+ user_token.access_token,
                    }
           })
           result = await result.json()
           console.log("result",result);
          
        }catch(err){
           console.error(err);
        }
    }
    return (
        <div className="ritekhela-main-content">

        
        <div className="ritekhela-main-section ritekhela-fixture-slider-full">
            <div className="container">
        <div className="widget widget_newsletter">
      
        <ReuasbleTitle titles={titles}/>
     
        <form>
            <label>Create A New Competition</label>
            <p>Fill all the fields please.</p>
            <input
             type="text"
             value={CompetitionName} 
             placeholder="Competition Name"
             onblur="if(this.value == '') { this.value ='Your Name'; }"
             onfocus="if(this.value =='Your Name') { this.value = ''; }"
             onChange={(e) => setCompetitionName(e.target.value)}
             />
             <input
             type="text"
             value={comp_description} 
             placeholder="Description"
             onblur="if(this.value == '') { this.value ='Your Name'; }"
             onfocus="if(this.value =='Your Name') { this.value = ''; }"
             onChange={(e) => setComp_description(e.target.value)}
             />
            {/* // utils={DateMomentUtils */}
            <MuiPickersUtilsProvider  utils={DateFnsUtils}>
            <DatePicker  
            value={start_date} 
            onChange={setStart_date}
             />
            </MuiPickersUtilsProvider>
            <br/>
            <MuiPickersUtilsProvider  utils={DateFnsUtils}>
            <DatePicker  
            value={end_date} 
            onChange={setEnd_date}
             />
            </MuiPickersUtilsProvider>
            <br/>
         
            <input type="submit" onClick={AddCompetition}value="Submit"/>
        </form>
    </div>
    </div>
    </div>
    </div>
    )
}


export default CreateCompetition
