import React ,{useState} from 'react'
import DateFnsUtils from '@date-io/date-fns'
import { alpha } from '@material-ui/core/styles'
import {
    DatePicker,
    TimePicker,
    DateTimePicker,
    MuiPickersUtilsProvider,
    KeyboardDatePicker,
  } from '@material-ui/pickers';
import DateMomentUtils from '@date-io/moment';
function SetGames(props) {
    const pending_comp = props.comp
    function showValues(e){
        e.preventDefault();
        console.log("ids ===",e.target.id)
    }
    const [selectedOption , setSelectedOption] = useState()
    const [selectedOptiontwo , setSelectedOptiontwo] = useState()
    const [start_date, setStart_date] = useState(new Date());
    const [S_month, S_day, S_year] = [start_date.getMonth(), start_date.getDate(), start_date.getFullYear()];
    const Start = S_year+"-"+S_month+"-"+S_day;
    const hours =("0"+ start_date.getHours()).slice(-2);
    const minutes =("0"+ start_date.getMinutes()).slice(-2);
    const seconds =("0"+ start_date.getSeconds()).slice(-2);
    const time = hours+":"+minutes+":"+seconds;
    console.log("time",time)
    console.log("hours",hours,"minutes", minutes,"seconds",seconds)

    console.log("date",Start)
    console.log(start_date);   
    console.log(selectedOption)
    console.log(selectedOptiontwo)
    const toke = localStorage.getItem('user-info');
    const user_token = JSON.parse(toke);
    const [message , setMessage] = useState();
    async function setGame(e){
         e.preventDefault();
         console.log(e)
         let credantials = {selectedOption,selectedOptiontwo,Start,time};
         console.log(credantials);
           try{
           let result = await fetch("http://127.0.0.1:8000/api/auth/teams_match",{
           method:'POST',
           body:JSON.stringify({
               team_one_id: credantials.selectedOption,
               team_two_id: credantials.selectedOptiontwo,
               date: credantials.Start,
               time:credantials.time,
              
           }),
           headers:{
               "Content-Type": "application/json",
               "Accept": "application/json",
               "Authorization" : "bearer"+ user_token.access_token,
                    }
           })
           result = await result.json()
           console.log("result",result.status);
           switch(result.status){
               case 'true':
                   setMessage(true);
                   break;
               case 'false':
                   setMessage(false);
                   break;
               default:
                   setMessage(result.status);        
           }
          
        }catch(err){
           console.error(err);
        }
    }
    return (
        <div className="ritekhela-main-content">

        <div className="ritekhela-main-section ritekhela-fixture-list-full">
            <div className="container">
               
                <div className="row">
                    <div className="col-md-12">
                        <div className="ritekhela-fancy-title">
                            <div className="ritekhela-fancy-title-inner">
                
                                <h2>Upcoming Match Fixture</h2>
                                <span>Select First VS Second Team And Set Date And Time For The Game</span>
                               <span> {message ? "data successfuly added" : null}</span>
                            </div>
                        </div>         
                        <div className="ritekhela-fixture ritekhela-matches-list">
                        <form >
                            <ul>
                                <li >
                                    <div className="ritekhela-cell">
                                                    <select value={selectedOption} onChange={ (e)=> setSelectedOption(e.target.value)}>
                                                    {pending_comp.map((pen_skl,index) =>
                                                    <option value={index}>{pen_skl}</option>
        
                                                        )} 
                                                    </select>
                                        <span id="2" >First Team</span>
                                        </div>
                                    <div className="ritekhela-cell">
                                        <a   href="#" className="ritekhela-fixture-flag">
                                            <img src="extra-images/fixer-flag-1.png" alt=""/></a>
                                        <span className="ritekhela-fixture-vs"><small>vs</small></span>
                                        <a href="#" className="ritekhela-fixture-flag ritekhela-next-flag">
                                            <img src="extra-images/fixer-flag-2.png" alt=""/></a>
                                            <br/><br/><br/>
                                            <MuiPickersUtilsProvider  utils={DateFnsUtils}>
                                            <DateTimePicker  
                                             value={start_date} 
                                             onChange={setStart_date}
                                            
                                            />
                                            </MuiPickersUtilsProvider> 
                                           
                                            
                                    </div>
                                  
                                    <div className="ritekhela-cell">
                                                        <select value ={selectedOptiontwo} onChange={ (e)=> setSelectedOptiontwo(e.target.value)}>
                                                    {pending_comp.map((pen_skl,index) =>
                                                    <option value={index}>{pen_skl}</option>
        
                                                        )} 
                                                    </select>
                                        <span id="2" >Second Team</span>
                                        </div>
                                    <div className="ritekhela-cell">
                                      <a href="#"id ="ss" style={{color:"white"}} onClick={setGame}><i class="fas fa-check" style={{color: "green"}}></i> confirm </a>       
                                    </div>
                                </li >
                                {/* <li>
                                    <div className="ritekhela-cell"><span>04 Sep. Monday 1:00pm</span></div>
                                    <div className="ritekhela-cell">
                                        <a href="#" className="ritekhela-fixture-flag"><img src="extra-images/fixer-flag-3.png" alt=""/> Liver Pool</a>
                                        <span className="ritekhela-fixture-vs"><small>vs</small></span>
                                        <a href="#" className="ritekhela-fixture-flag ritekhela-next-flag"><img src="extra-images/fixer-flag-4.png" alt=""/> South United</a>
                                    </div>
                                    <div className="ritekhela-cell">
                                      <a href="#"><i class="fas fa-check"></i> confirm </a>       
                                    </div>
                                </li> */}
                            </ul>
                        </form>
                        </div>
                        <div className="ritekhela-pagination">
                            <ul>
                                <li><a href="#"><i className="fa fa-angle-left"></i></a></li>
                                <li><a href="#">1</a></li>
                                <li><a href="#">2</a></li>
                                <li><a href="#">3</a></li>
                                <li><a href="#">4</a></li>
                                <li><a href="#">5</a></li>
                                <li><a href="#"><i className="fa fa-angle-right"></i></a></li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    )
}

export default SetGames
