import React from 'react'

function SetResults(props) {
    const setResults = props.score
    const gamesId = setResults.game_id
    const teamOne = setResults.team_one
    const teamTwo = setResults.team_two
    console.log("gamesIDsssssssss", gamesId)
    console.log("team 0ne ", teamOne)
    console.log("team two", teamTwo)
    console.log("setResults  s",setResults)
    return (
       
        <div className="ritekhela-main-content">

        <div className="ritekhela-main-section ritekhela-fixture-list-full">
            <div className="container">
               
                     
            
                <div className="row">
                    <div className="col-md-12">
                        <div className="ritekhela-fancy-title">
                            <div className="ritekhela-fancy-title-inner">
                
                                <h2>Upcoming Match Fixture</h2>
                                <span>Select First VS Second Team And Set Date And Time For The Game</span>
                               <span> </span>
                            </div>
                        </div>  
                        {gamesId.map(game =>   
                        <div className="ritekhela-fixture ritekhela-matches-list">
                        
                        
                   
            
                        <form >
                            
                        <h1>{game} </h1> 
                              
                            <ul>
                            
                           
                                <li >
                             
                                {teamOne.map((team) =>
                                      (team).map(t=> 
                                    <div className="ritekhela-cell">

                                                    <select >
                                                 
                                                    <option ></option>
        
                                                       
                                                    </select>
                                                   <h1>{t.team_name}{t.id}</h1> 
                                        <span id="2" >First Team</span>
                                        </div>
                                         ))}
                                   
                                      
                                       
                                    <div className="ritekhela-cell">
                                        <a   href="#" className="ritekhela-fixture-flag">
                                            <img src="extra-images/fixer-flag-1.png" alt=""/></a>
                                        <span className="ritekhela-fixture-vs"><small>vs</small></span>
                                        <a href="#" className="ritekhela-fixture-flag ritekhela-next-flag">
                                            <img src="extra-images/fixer-flag-2.png" alt=""/></a>
                                            <br/><br/><br/>
                                         
                                           
                                            
                                    </div>
                                    
                                    
                                        {teamTwo.map((team2) =>
                                        (team2).map(t2=>
                                    <div className="ritekhela-cell">
                                   
                                                        <select >
                                             
                                                    <option ></option>
        
                                                     
                                                    </select>
                                                    <h1>{t2.team_name}{t2.id}</h1>
                                        <span id="2" >Second Team</span>
                                       
                                        </div>
                                        ))} 
                                       
                                          
                                       
                                    <div className="ritekhela-cell">
                                      <a href="#"id ="ss" style={{color:"white"}} ><i class="fas fa-check" style={{color: "green"}}></i> confirm </a>       
                                    </div>
                                   
                                </li >
                                
                              
                                   

                              
                            </ul>
                           
                           
                        </form>
                        
                        
                      
                      
                        </div>
                        )}
                        
                        <div className="ritekhela-pagination">
                            <ul>
                                <li><a href="#"><i className="fa fa-angle-left"></i></a></li>
                                <li><a href="#">1</a></li>
                                <li><a href="#">2</a></li>
                                <li><a href="#">3</a></li>
                                <li><a href="#">4</a></li>
                                <li><a href="#">5</a></li>
                                <li><a href="#"><i className="fa fa-angle-right"></i></a></li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    )
}

export default SetResults
