import React,{ useState } from 'react'
import ReuasbleTitle from './ReuasbleTitle';
function AddCoach() {
    const [titles , setTitle] = useState([
        {title : 'Coordinator Page  Add Coach'}]);
        const [coachName, setCoachName] = useState();
        const [coachEmail, setCoachEmail] = useState();
        const [coachPassword, setCoachPassword] = useState();
        const [confirm_password,setConfirm_password]=useState();
        const toke = localStorage.getItem('user-info');
        const user_token = JSON.parse(toke);
        //console.log(user_token.access_token);
        console.log('token is : ',toke);   
        
        async function AddCoach(e)
        {
            e.preventDefault();
            let credantials = { coachName,coachEmail,coachPassword,confirm_password}
            console.log(credantials)
            try
            {
                let result = await fetch("http://127.0.0.1:8000/api/auth/register_coach",{
                method:'POST',
                body:JSON.stringify({
                    name:credantials.coachName,
                    email:credantials.coachEmail,
                    password: credantials.coachPassword,
                    password_confirmation: credantials.confirm_password,
                }),
                headers:
                {
                    "Content-Type": "application/json",
                    "Accept": "application/json",
                    "Authorization" : "bearer"+ user_token.access_token,  
                }
                })
                result = await result.json()
                console.log("result :" ,result)
            }catch(err)
            {
                console.error(err);
            }


        }

    return (
        <div className="ritekhela-main-content">

        
        <div className="ritekhela-main-section ritekhela-fixture-slider-full">
            <div className="container">
        <div className="widget widget_newsletter">
      
        <ReuasbleTitle titles={titles}/>
     
        <form>
            <label>Add Coach</label>
            <p>Coach Information.</p>
            <input
             type="text"
             value={coachName} 
             placeholder="Coach Name"
             onblur="if(this.value == '') { this.value ='Your Name'; }"
             onfocus="if(this.value =='Your Name') { this.value = ''; }"
             onChange={(e) => setCoachName(e.target.value)}
             />
            <input
            type="text"
            value={coachEmail}
            placeholder="Email "
            onblur="if(this.value == '') { this.value ='Enter Your email'; }" 
            onfocus="if(this.value =='Enter Your email') { this.value = ''; }"
            onChange ={(e) => setCoachEmail(e.target.value)}
            />
                  <input
             type="password"
             value={coachPassword} 
             placeholder="Enter Password"
             onblur="if(this.value == '') { this.value ='Your Name'; }"
             onfocus="if(this.value =='Your Name') { this.value = ''; }"
             onChange={(e) =>setCoachPassword(e.target.value)}
             /> 
             <input
            type="password"
            value={confirm_password} 
            placeholder="confirm your password"
            onChange={(e) => setConfirm_password(e.target.value)}
            />
            <input type="submit" onClick={AddCoach} value="Add Coach"/>
        </form>
    </div>
    </div>
    </div>
    </div>
    )
}

export default AddCoach
