import React from 'react'

function PlayersContent(props) {
    const  players = props.players_data;
    return (
        <div className="ritekhela-main-content">
               <div className="ritekhela-main-section ritekhela-fixture-list-full">
                <div className="container">
                    <div className="row">
                        <div className="col-md-12">   
                            <div className="ritekhela-team ritekhela-team-view1">
                                
                                <ul className="row">
                                {players.map((player,index)=>
                                    <li className="col-md-3">
                                        <figure>
                                       {/** src="extra-images/team-view1-1.jpg"  */}
                                       {/**http://localhost:8000/image/download.jpg */}
                                            <a href="#"><img src={"http://localhost:8000/image/" + player.file } alt=""/></a>
                                            <figcaption>
                                                <a href="#" className="fab fa-facebook-f"></a>
                                                <a href="#" className="fab fa-twitter"></a>
                                                <a href="#" className="fab fa-instagram"></a>
                                                <a href="#" className="fab fa-dribbble"></a>
                                            </figcaption>
                                        </figure>
                                        <div className="ritekhela-team-view1-text">
                                            <h2><a href="#">{player.first_name}</a></h2>
                                            <span>Forword</span>
                                            <p>{player.last_name}</p>
                                            <a href="#" className="ritekhela-team-view1-btn">Read More <i className="fa fa-angle-right"></i></a>
                                        </div>
                                    </li>
                                    )}
                                </ul>
                               
                            </div>
                            <div className="ritekhela-pagination">
                                <ul>
                                    <li><a href="#"><i className="fa fa-angle-left"></i></a></li>
                                    <li><a href="#">1</a></li>
                                    <li><a href="#">2</a></li>
                                    <li><a href="#">3</a></li>
                                    <li><a href="#">4</a></li>
                                    <li><a href="#">5</a></li>
                                    <li><a href="#"><i className="fa fa-angle-right"></i></a></li>
                                </ul>
                            </div> 
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default PlayersContent
