import React ,{useState} from 'react'
import {useHistory,Link}  from 'react-router-dom'
function LoginModalBox() {
    const [email, setEmail] = useState();
    const [password, setPassword] = useState();
    const [popbutton, setPopbutton] = useState(true);
    const history = useHistory();
   
   async function login(){
       try{
        let credantials = {email,password};
        console.log(credantials);
        let result = await fetch("http://127.0.0.1:8000/api/auth/login",{
        method:'POST',
        headers:{
            "Content-Type": "application/json",
            "Accept": "application/json"
             },
        body:JSON.stringify({
            email: credantials.email,
            password: credantials.password,
            }),     
        })
        result = await result.json()
        localStorage.setItem("user-info",JSON.stringify(result))
        let user = result.user
        console.log("userInfo id :",user.id)
        setPopbutton(false);
        console.log("button press ", popbutton);
        if(user.role === 1){history.push("/admin")}
        if(user.role === 2){history.push("/coordinator")}
        if(user.role === 3){history.push("/coach")}
        //history.push("/player")
    
    }catch(err){
        console.error(err);
    }
   }
    return (
        <div>
        <div className="loginmodalbox modal fade" id="ritekhelamodalcenter" tabIndex="-1">
          
        <div className="modal-dialog modal-dialog-centered" role="document">
           <div className="modal-content">
              <div className="modal-body ritekhela-bgcolor-two">
           
                 <h5 className="modal-title">Login To Your Account</h5>
                 <a href="#" className="close ritekhela-bgcolor-two" data-dismiss="modal" aria-label="Close">
                     <i className="fa fa-times"></i>
                 </a>
                 <div className="loginmodalbox-search">
                   
                <input
                type="text"
                value={email} 
                placeholder="Email"
                onChange={(e) => setEmail(e.target.value)}
                />
                <input
                type="password"
                value={password} 
                placeholder="Password"
                onChange={(e) => setPassword(e.target.value)}
                />
                <input type="submit" onClick={login} value="Login Now" data-dismiss="modal" className="ritekhela-bgcolor"/>
                     <a href="#" className="loginmodalbox-forget">Forget Password?</a>
                 </div>
                
              </div>
           </div>
        </div>
 
     </div>
     </div>
    
    )
}

export default LoginModalBox
