import React,{useState , useEffect} from 'react'
import AllMatchesContent from '../components/AllMatchesContent'
import Footer from '../components/Footer'
import Header from '../components/Header'
import LoginModalBox from '../components/LoginModalBox'
import SearchBox from '../components/SearchBox'
import SignUpModaBox from '../components/SignUpModaBox'

import ReuasableHeader from '../components/ReuasableHeader'
import ReuasableSubHeader from '../components/ReuasableSubHeader'
import axios from 'axios'
function AllMatches() {
    let HeaderData  ={
        login :'Login',
        signup :'Signup',
         home:'Home', 
        features:'Features',
        players:'Players',
        ournews:'Owr News ',
        ourshop:'Our Shop' ,
        contactus:'Contact us'
    };
        
      

      let SubHeaderData  =[{
        header:'MATCHES',
        title :'HOME',
        path :'FIXTURES',
         path_two:'ALLMATCHES',}
        
      ];
      const toke = localStorage.getItem('user-info');
      const user_token = JSON.parse(toke);
      const [games ,setGames] = useState(null)
      useEffect(() => {
        fetchGames();
        
      }, [])
      async function fetchGames()
      {
      try
      {
        let result = await axios.get("http://127.0.0.1:8000/api/auth/show_games",{
        method:'GET',
        headers:
        {
            "Content-Type": "application/json",
            "Accept": "application/json",
            "Authorization" : "bearer"+ user_token.access_token,  
        }
        })
        let games = await result.data
        console.log(games)
      const  data = result;//.school
      setGames(games);
      console.log("result :" ,data)  
      }catch(err)
      {
        console.error(err);
      }
      }
      console.log("games" , games)
    return (
        <>
        <div className="ritekhela-wrapper">
          
            {/* {games.map(game=> <h1> {game}</h1>
            
            )} */}
            {/* <Header/> */}
            <ReuasableHeader headers = {HeaderData} />
            <ReuasableSubHeader Sub_data = {SubHeaderData}/>
            {/* <SubHeadermatch/> */}
            <AllMatchesContent matche ={games}/>
            <Footer/>
            
        </div>
        <LoginModalBox/>
        <SignUpModaBox/>
        <SearchBox/>
        </>
)
}

export default AllMatches
