import React ,{useState, useEffect} from 'react'
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import DeleteIcon from '@mui/icons-material/Delete';
import ReuasbleTitle from './ReuasbleTitle';
import { usePickerState } from '@material-ui/pickers';
function Table(props) {
  const schools = props.data;
  const approve = (e)=> props.onApprove(e.target.id)
  const decline = (e)=> props.onDelete(e.target.id)
  console.log("table ",schools)
  const [titles , setTitle] = useState([
    {title : 'Admin Page'}
]);
const toke = localStorage.getItem('user-info');
const user_token = JSON.parse(toke);


    return (
      
      <div className="ritekhela-main-content">

        
      <div className="ritekhela-main-section ritekhela-fixture-slider-full">
          <div className="container">
        
            <ReuasbleTitle titles={titles}/>
         
            {/* {skl.map((u,i)=><h1 key = {u.school_name}>
              {i}</h1>)} */}
        <div>
        <table >
        <thead>
          <tr>
            <th scope="col">#</th>
            <th scope="col">Account</th>
            <th scope="col">Details</th>
            <th scope="col">Action</th>
            {/* <th scope="col">Decline</th> */}
          </tr>
        </thead>
        
        <tbody>
         {schools.map(user=>
          <tr>
            <th scope="row" >{user.id}</th>
            <td>{user.school_name}</td>
            <td>{user.school_address}</td>
            <td>
            {/* <Button variant="contained" color="success" onClick={approve} id = {user.id}>
             Success
            </Button> */}
            <p>
           
            <i id = {user.id} onClick={decline} className="fas fa-trash-restore-alt" style={{color: "red"}} ></i>
              
            &nbsp; &nbsp; &nbsp;&nbsp;&nbsp;
           
            <i id = {user.id} onClick={approve} className="fas fa-check" style={{color: "green"}} ></i>
              
            </p>
            </td>
            {/* <td><p className="ritekhela-submit">
             <Button variant="outlined" onClick={decline}  id = {user.id} startIcon={<DeleteIcon />}>
            Delete
            </Button></p></td>     */}
            {/* <a href ="#"><i className="fas fa-check" style={{color: "green"}} onClick={approve} id = {user.id}></i></a></p></td> */}  
            
          </tr>
         )}
        </tbody>
      
      </table>
      </div>
      </div>
      </div>
      </div>
    )
}

export default Table
