
import React from 'react'

function ReuasbleTitle(props) {
    const titles = props.titles;
    const listItems = titles.map((title) => 
    <h2 key={title.toString()}>
        {title.title}
    </h2>
    );
    return (
        <div>
        <div className="ritekhela-fancy-title-two">
           {listItems}  
        </div>
        </div>
    )
}

export default ReuasbleTitle
