import React from 'react'
import {Link} from "react-router-dom"
function ReuasableHeader(props) {
    const data = props.headers;
    console.log(data);
    return (  
        <header id="ritekhela-header" className="ritekhela-header-one">
    <div className="ritekhela-topstrip">
        <div className="container">
            <div className="row">
                <aside className="col-md-6">
                    <strong>YOUTH COMPETITIONS</strong>
                    <div className="ritekhela-latest-news-slider">
                        <div className="ritekhela-latest-news-slider-layer">your welcome lets meet !!  </div>
                        <div className="ritekhela-latest-news-slider-layer">keep in touch with the latest competitions</div>
                    </div>
                </aside>
                <aside className="col-md-6">
                    <ul className="ritekhela-user-strip">
                        <li><a href="#" data-toggle="modal" data-target="#ritekhelamodalcenter"><i className="fa fa-user-alt"></i> {data.login}</a></li>
                        <li><a href="#" data-toggle="modal" data-target="#ritekhelamodalrg"> <i className="fa fa-sign-in-alt"></i>{data.signup}</a> </li>
                    </ul>
                </aside>
            </div>
        </div>
    </div>
            <div className="ritekhela-main-header">
        <div className="container">
            <div className="row">
                <div className="col-md-12">
                    <a href="#" className="ritekhela-logo"><img src="images/logo.png" alt=""/></a>
                    <div className="ritekhela-right-section">
                        <div className="ritekhela-navigation">
                            <span className="ritekhela-menu-link">
                                <span className="menu-bar"></span>
                                <span className="menu-bar"></span>
                                <span className="menu-bar"></span>
                            </span>
                            <nav id="main-nav">
                                <ul id="main-menu" className="sm sm-blue">
                                    <li className="active"><Link to="/">{data.home}</Link></li>
                                    <li className="megamenu-wrap"><Link to="/team">{data.features}</Link>
                                   
                                    </li>
                                    <li>
                                        <Link to="/player">{data.players}</Link>
                                    
                                    </li>
                                    <li><a href="#">{data.ournews}</a>
                                      
                                    </li>
                                    <li><a href="#">{data.ourshop}</a>
                                    </li>
                                    <li><a href="contact-us.html">{data.contactus}</a></li>
                                </ul>
                            </nav>
                        </div>
                        <ul className="ritekhela-navsearch">
                            <li><a href="#" className="ritekhela-open-cart"><i className="fab fa-opencart"></i></a>
                            </li>
                            <li><a href="#" data-toggle="modal" data-target="#ritekhelamodalsearch"><i className="fa fa-search"></i></a></li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    </div>

        </header>
    )
}

export default ReuasableHeader
